
#app {
  font-family: Arial, 'Avenir', Helvetica, Arial, sans-serif;
  font-size: 1rem;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  text-align: center;
  color: #2c3e50;
  width: 800px;
  max-width: 100%;
  margin: 6rem auto;
}
h2 {
  font-family: Montserrat, sans-serif;
  font-size: 2rem;
  margin-bottom: 1rem;
}
.motto {
  margin-bottom: 1rem;
}
.controls {
  margin-top: 0.5rem;
}
.controls input {
  padding: 0 0.2rem;
  line-height: 1.6rem;
}
.controls label {
  height: 2rem;
  line-height: 2rem;
  display: inline-block;
  vertical-align: text-bottom;
}
code {
  padding: 0.5rem;
  font-family: monospace;
  border: 1px solid #333;
  text-align: left;
  white-space: pre;
  display: block;
  max-width: 100%;
  width: auto;
  margin: auto;
}


